body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: linear-gradient(#e66465, #9198e5) no-repeat;
  background-attachment: fixed;
  /* Keep the background fixed during scrolling */
  /* Ensure the background covers the entire page */
  /* Center the background */
  /* Ensure body height extends beyond viewport */
}

html,
body {
  height: 100%;
}
.App {
  text-align: center;
}

html {
  scroll-behavior: smooth;
} 
body{
  margin: 0; padding: 0; height: 100%; width: 100%;
}

.center-column {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.center-row {
  display: flex;
  flex-direction: row;
  justify-content: center
}
.end-row-{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

button {
  cursor: pointer;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.cursor{
  cursor:pointer
}
.tilt {
	background: linear-gradient(#e66465, #9198e5) no-repeat;
}
